/* eslint-disable camelcase */
import React, { useEffect, useState } from 'react';
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  Label,
} from 'reactstrap';
import { connect } from 'react-redux';
import IntlMessages from '../../../../helpers/IntlMessages';

import { wait } from '../../../../helpers/Utils';
import { NotificationManager } from '../../../../components/common/react-notifications';
import useRequest from '../../../../hooks/use-request';
import { getAllStatus } from '../../../../redux/actions';

const EditModal = ({
  modalOpen,
  currentUserData: currentData,
  toggleModal,
  accessToken,
  filters,
  getAllStatusAction,
}) => {
  const [values, setValues] = useState({ ...currentData });

  useEffect(() => {
    setValues({ ...currentData });
  }, [currentData]);

  const [doRequest, loading] = useRequest({
    url: `/client/edit-status/${values.id}`,
    method: 'PUT',
    headers: {
      Authorization: accessToken,
    },
    data: values,
    nextSuccess: () => {
      NotificationManager.success(
        'Status Edited Successfully',
        'Status Operation Success',
        3000,
        null,
        null,
        ''
      );
      getAllStatusAction(accessToken, filters);
      wait(1200, () => {
        setValues({ ...currentData, password: 'null' });
        toggleModal(false);
      });
    },
    nextError: (error) => {
      NotificationManager.error(
        error,
        'Status Operation Error',
        3000,
        null,
        null,
        ''
      );
    },
  });

  const handleChange = (event) => {
    setValues({ ...values, [event.target.id]: event.target.value });
  };

  const handleSubmit = () => {
    if (values.status === '') {
      NotificationManager.error(
        'Status name can not be empty!',
        'Status Operation Error',
        3000,
        null,
        null,
        ''
      );
    } else {
      doRequest();
    }
  };

  return (
    <Modal
      isOpen={modalOpen}
      toggle={toggleModal}
      wrapClassName="modal-right"
      backdrop="static"
    >
      <ModalHeader toggle={toggleModal}>
        <IntlMessages id="pages.edit-service-modal-title" />
      </ModalHeader>
      <ModalBody>
        <Label>
          <IntlMessages id="pages.app-status" />
        </Label>
        <Input
          id="status"
          value={values.status}
          required
          onChange={handleChange}
        />
      </ModalBody>
      <ModalFooter>
        <Button color="danger" outline onClick={toggleModal}>
          <IntlMessages id="pages.cancel" />
        </Button>
        <Button
          id="edit-user"
          color="primary"
          className={`btn-shadow btn-multiple-state ${
            loading ? 'show-spinner' : ''
          }`}
          onClick={handleSubmit}
        >
          <span className="spinner d-inline-block">
            <span className="bounce1" />
            <span className="bounce2" />
            <span className="bounce3" />
          </span>
          <span className="label">
            <IntlMessages id="pages.submit" />
          </span>
        </Button>
      </ModalFooter>
    </Modal>
  );
};

const mapStateToProps = ({ authUser, serviceApp }) => {
  const { accessToken } = authUser;
  const { filters } = serviceApp;
  return { accessToken, filters };
};

export default connect(mapStateToProps, {
  getAllStatusAction: getAllStatus,
})(EditModal);
