/* eslint-disable prefer-promise-reject-errors */
import React, { useState } from 'react';
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  Label,
} from 'reactstrap';
import { connect } from 'react-redux';

import IntlMessages from '../../../../helpers/IntlMessages';

import { wait } from '../../../../helpers/Utils';
import { NotificationManager } from '../../../../components/common/react-notifications';
import { getAllStatus } from '../../../../redux/actions';
import useRequest from '../../../../hooks/use-request';

const INIT_STATE = () => ({
  status: '',
});

const AddNewModal = ({
  modalOpen,
  toggleModal,
  accessToken,
  filters,
  getAllStatusAction,
}) => {
  const [values, setValues] = useState(INIT_STATE);

  const handleChange = (event) => {
    setValues({ ...values, [event.target.id]: event.target.value });
  };

  const [doRequest, loading] = useRequest({
    url: '/client/add-status',
    method: 'POST',
    headers: {
      Authorization: accessToken,
    },
    data: values,
    nextSuccess: () => {
      NotificationManager.success(
        'Status Added Successfully',
        'Status Operation Success',
        3000,
        null,
        null,
        ''
      );
      getAllStatusAction(accessToken, filters);
      wait(1200, () => {
        setValues(INIT_STATE);
        toggleModal(false);
      });
    },
    nextError: () => {
      NotificationManager.error(
        'Status Already Exist!',
        'Status Operation Error',
        3000,
        null,
        null,
        ''
      );
    },
  });

  const handleSubmit = () => {
    if (values.status === '') {
      NotificationManager.error(
        'Status name can not be empty!',
        'Status Operation Error',
        3000,
        null,
        null,
        ''
      );
    } else {
      doRequest();
    }
  };

  return (
    <Modal
      isOpen={modalOpen}
      toggle={() => {
        toggleModal();
        setValues(INIT_STATE);
      }}
      wrapClassName="modal-right"
      backdrop="static"
    >
      <ModalHeader
        toggle={() => {
          toggleModal();
          setValues(INIT_STATE);
        }}
      >
        <IntlMessages id="pages.add-new-status-modal-title" />
      </ModalHeader>
      <ModalBody>
        <Label>
          <IntlMessages id="pages.app-status" />
        </Label>
        <Input
          id="status"
          value={values.status}
          required
          onChange={handleChange}
        />
      </ModalBody>
      <ModalFooter>
        <Button color="danger" outline onClick={toggleModal}>
          <IntlMessages id="pages.cancel" />
        </Button>
        <Button
          id="add-new-user"
          color="primary"
          className={`btn-shadow btn-multiple-state ${
            loading ? 'show-spinner' : ''
          }`}
          onClick={handleSubmit}
        >
          <span className="spinner d-inline-block">
            <span className="bounce1" />
            <span className="bounce2" />
            <span className="bounce3" />
          </span>
          <span className="label">
            <IntlMessages id="pages.submit" />
          </span>
        </Button>
      </ModalFooter>
    </Modal>
  );
};

const mapStateToProps = ({ authUser, statusApp }) => {
  const { accessToken } = authUser;
  const { filters } = statusApp;
  return { accessToken, filters };
};

export default connect(mapStateToProps, {
  getAllStatusAction: getAllStatus,
})(AddNewModal);
