import React from 'react';
import { ContextMenu, MenuItem } from 'react-contextmenu';

const ContextMenuContainer = ({
  onContextMenu,
  onContextMenuClick,
  showButtons,
}) => {
  const { showAdd, showEdit } = showButtons;

  if (!(showAdd && showEdit)) return null;

  return (
    <ContextMenu
      id="menu_id"
      onShow={(e) => onContextMenu(e, e.detail.data)}
      style={{ borderRadius: '16px' }}
    >
      {showEdit && (
        <MenuItem
          onClick={onContextMenuClick}
          data={{ action: 'edit' }}
          className="text-primary"
        >
          <i className="simple-icon-pencil" /> <span>Edit</span>
        </MenuItem>
      )}
      {showAdd && (
        <MenuItem
          onClick={onContextMenuClick}
          data={{ action: 'create-new' }}
          className="text-primary"
        >
          <i className="simple-icon-plus" /> <span>Add New</span>
        </MenuItem>
      )}
    </ContextMenu>
  );
};

export default ContextMenuContainer;
