import React, { useState } from 'react';
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  Label,
  Row,
  CustomInput,
} from 'reactstrap';
import { connect } from 'react-redux';

import IntlMessages from '../../../../helpers/IntlMessages';

import { wait } from '../../../../helpers/Utils';
import { NotificationManager } from '../../../../components/common/react-notifications';
import { getAllSubCategory } from '../../../../redux/actions';
import useRequest from '../../../../hooks/use-request';
import { Colxx } from '../../../../components/common/CustomBootstrap';

const INIT_STATE = () => ({
  subcategory_name: '',
  is_enabled: true,
});

const AddNewModal = ({
  modalOpen,
  toggleModal,
  accessToken,
  filters,
  getAllSubCategoryAction,
}) => {
  const [values, setValues] = useState(INIT_STATE);

  const [doRequest, loading] = useRequest({
    url: '/client/add-subcategory',
    method: 'POST',
    headers: {
      Authorization: accessToken,
    },
    data: values,
    nextSuccess: () => {
      NotificationManager.success(
        'Sub Category Added Successfully',
        'Sub Category Operation Success',
        3000,
        null,
        null,
        ''
      );
      getAllSubCategoryAction(accessToken, filters);
      wait(1200, () => {
        setValues(INIT_STATE);
        toggleModal(false);
      });
    },
    nextError: () => {
      NotificationManager.error(
        'Sub Category Already Exist!',
        'Sub Category Operation Error',
        3000,
        null,
        null,
        ''
      );
    },
  });

  const handleChange = (event) => {
    setValues({ ...values, [event.target.id]: event.target.value });
  };

  const handleToggle = (event) => {
    setValues({ ...values, [event.target.id]: !values[event.target.id] });
  };

  const handleSubmit = () => {
    if (values.subcategory_name === '') {
      NotificationManager.error(
        'Sub Category name can not be empty!',
        'Sub Category Operation Error',
        3000,
        null,
        null,
        ''
      );
    } else {
      doRequest();
    }
  };

  return (
    <Modal
      isOpen={modalOpen}
      toggle={() => {
        toggleModal();
        setValues(INIT_STATE);
      }}
      wrapClassName="modal-right"
      backdrop="static"
    >
      <ModalHeader
        toggle={() => {
          toggleModal();
          setValues(INIT_STATE);
        }}
      >
        <IntlMessages id="pages.add-new-sub-category-modal-title" />
      </ModalHeader>
      <ModalBody>
        <Label>
          <IntlMessages id="pages.app-sub-category" />
        </Label>
        <Input
          id="subcategory_name"
          value={values.subcategory_name}
          required
          onChange={handleChange}
        />

        <Row className="mt-4">
          <Colxx className="d-flex flex-column justify-content-center">
            <Label className="m-0 align-middle">
              <IntlMessages id="pages.user-is-enabled" />
            </Label>
          </Colxx>
          <Colxx className="d-flex flex-column justify-content-center">
            <CustomInput
              id="is_enabled"
              type="checkbox"
              checked={values.is_enabled}
              onChange={handleToggle}
            />
          </Colxx>
        </Row>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" outline onClick={toggleModal}>
          <IntlMessages id="pages.cancel" />
        </Button>
        <Button
          id="add-new-sub-category"
          color="primary"
          className={`btn-shadow btn-multiple-state ${
            loading ? 'show-spinner' : ''
          }`}
          onClick={handleSubmit}
        >
          <span className="spinner d-inline-block">
            <span className="bounce1" />
            <span className="bounce2" />
            <span className="bounce3" />
          </span>
          <span className="label">
            <IntlMessages id="pages.submit" />
          </span>
        </Button>
      </ModalFooter>
    </Modal>
  );
};

const mapStateToProps = ({ authUser, subCategoryApp }) => {
  const { accessToken } = authUser;
  const { filters } = subCategoryApp;
  return { accessToken, filters };
};

export default connect(mapStateToProps, {
  getAllSubCategoryAction: getAllSubCategory,
})(AddNewModal);
