import React, { useState } from 'react';
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  Label,
  Row,
  CustomInput,
} from 'reactstrap';
import { connect } from 'react-redux';
import Select from 'react-select';
import makeAnimated from 'react-select/animated';

import IntlMessages from '../../../../helpers/IntlMessages';

import { wait } from '../../../../helpers/Utils';
import { NotificationManager } from '../../../../components/common/react-notifications';
import { getAllCategory } from '../../../../redux/actions';
import useRequest from '../../../../hooks/use-request';
import { Colxx } from '../../../../components/common/CustomBootstrap';
import CustomSelectInput from '../../../../components/common/CustomSelectInput';

const INIT_STATE = () => ({
  category_name: '',
  is_enabled: false,
  sub_category: [],
});

const AddNewModal = ({
  modalOpen,
  toggleModal,
  accessToken,
  subCategory,
  filters,
  getAllCategoryAction,
}) => {
  const [values, setValues] = useState(INIT_STATE);

  const animatedComponents = makeAnimated(CustomSelectInput);

  const [doRequest, loading] = useRequest({
    url: '/client/add-category',
    method: 'POST',
    headers: {
      Authorization: accessToken,
    },
    data: values,
    nextSuccess: () => {
      NotificationManager.success(
        'Category Added Successfully',
        'Category Operation Success',
        3000,
        null,
        null,
        ''
      );
      getAllCategoryAction(accessToken, filters);
      wait(1200, () => {
        setValues(INIT_STATE);
        toggleModal(false);
      });
    },
    nextError: (error) => {
      NotificationManager.error(
        error,
        'Category Operation Error',
        3000,
        null,
        null,
        ''
      );
    },
  });

  const handleChange = (event) => {
    setValues({ ...values, [event.target.id]: event.target.value });
  };

  const handleSelectChange = (newData) => {
    setValues({ ...values, sub_category: newData });
  };

  const handleToggle = (event) => {
    setValues({ ...values, [event.target.id]: !values[event.target.id] });
  };

  const handleSubmit = () => {
    if (values.category_name === '') {
      NotificationManager.error(
        'Category name cannot be blank',
        'Category Operation Error',
        3000,
        null,
        null,
        ''
      );
    } else if (values.sub_category.length === 0) {
      NotificationManager.error(
        'Category name can not be blank',
        'Category Operation Error',
        3000,
        null,
        null,
        ''
      );
    } else {
      doRequest();
    }
  };

  return (
    <Modal
      isOpen={modalOpen}
      toggle={() => {
        toggleModal();
        setValues(INIT_STATE);
      }}
      wrapClassName="modal-right"
      backdrop="static"
    >
      <ModalHeader
        toggle={() => {
          toggleModal();
          setValues(INIT_STATE);
        }}
      >
        <IntlMessages id="pages.add-new-category-modal-title" />
      </ModalHeader>
      <ModalBody>
        <Label>
          <IntlMessages id="pages.app-category" />
        </Label>
        <Input
          id="category_name"
          value={values.category_name}
          required
          onChange={handleChange}
        />

        <Row className="mt-4">
          <Colxx className="d-flex flex-column justify-content-center">
            <Label className="m-0 align-middle">
              <IntlMessages id="pages.user-is-enabled" />
            </Label>
          </Colxx>
          <Colxx className="d-flex flex-column justify-content-center">
            <CustomInput
              id="is_enabled"
              type="checkbox"
              checked={values.is_enabled}
              onChange={handleToggle}
            />
          </Colxx>
        </Row>

        <Label className="m-0 align-middle mt-4">
          <IntlMessages id="pages.select-sub-category" />
        </Label>
        <Select
          name="select-sub-category"
          components={animatedComponents}
          isMulti
          isClearable
          isSearchable
          className="react-select mt-2"
          classNamePrefix="react-select"
          value={values.sub_category}
          getOptionValue={(option) => option.id}
          getOptionLabel={(option) => option.subcategory_name}
          onChange={handleSelectChange}
          options={subCategory.data}
          isOptionDisabled={(option) => option.is_enabled === false}
        />
      </ModalBody>
      <ModalFooter>
        <Button color="danger" outline onClick={toggleModal}>
          <IntlMessages id="pages.cancel" />
        </Button>
        <Button
          id="add-new-user"
          color="primary"
          className={`btn-shadow btn-multiple-state ${
            loading ? 'show-spinner' : ''
          }`}
          onClick={handleSubmit}
        >
          <span className="spinner d-inline-block">
            <span className="bounce1" />
            <span className="bounce2" />
            <span className="bounce3" />
          </span>
          <span className="label">
            <IntlMessages id="pages.submit" />
          </span>
        </Button>
      </ModalFooter>
    </Modal>
  );
};

const mapStateToProps = ({ authUser, categoryApp, subCategoryApp }) => {
  const { accessToken } = authUser;
  const { subCategory } = subCategoryApp;
  const { filters } = categoryApp;
  return { accessToken, subCategory, filters };
};

export default connect(mapStateToProps, {
  getAllCategoryAction: getAllCategory,
})(AddNewModal);
