/* eslint-disable camelcase */
import React, { useEffect, useState } from 'react';
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  Label,
  Row,
  CustomInput,
} from 'reactstrap';
import { connect } from 'react-redux';
import IntlMessages from '../../../../helpers/IntlMessages';

import { wait } from '../../../../helpers/Utils';
import { NotificationManager } from '../../../../components/common/react-notifications';
import useRequest from '../../../../hooks/use-request';
import { getAllService } from '../../../../redux/actions';
import { Colxx } from '../../../../components/common/CustomBootstrap';

const EditModal = ({
  modalOpen,
  currentData,
  toggleModal,
  accessToken,
  filters,
  getAllServiceAction,
}) => {
  const [values, setValues] = useState({ ...currentData });

  useEffect(() => {
    setValues({ ...currentData });
  }, [currentData]);

  const [doRequest, loading] = useRequest({
    url: `/client/edit-service/${values.id}`,
    method: 'PUT',
    headers: {
      Authorization: accessToken,
    },
    data: values,
    nextSuccess: () => {
      NotificationManager.success(
        'Status Edited Successfully',
        'Status Operation Success',
        3000,
        null,
        null,
        ''
      );
      getAllServiceAction(accessToken, filters);
      wait(1200, () => {
        setValues({ ...currentData });
        toggleModal(false);
      });
    },
    nextError: (error) => {
      NotificationManager.error(
        error,
        'Status Operation Error',
        3000,
        null,
        null,
        ''
      );
    },
  });

  const handleChange = (event) => {
    setValues({ ...values, [event.target.id]: event.target.value });
  };

  const handleToggle = (event) => {
    setValues({ ...values, [event.target.id]: !values[event.target.id] });
  };

  const handleSubmit = () => {
    if (values.service_name === '') {
      NotificationManager.error(
        'Service name can not be empty.',
        'Service Operation Error',
        3000,
        null,
        null,
        ''
      );
    } else {
      doRequest();
    }
  };

  return (
    <Modal
      isOpen={modalOpen}
      toggle={toggleModal}
      wrapClassName="modal-right"
      backdrop="static"
    >
      <ModalHeader toggle={toggleModal}>
        <IntlMessages id="pages.edit-service-modal-title" />
      </ModalHeader>
      <ModalBody>
        <Label>
          <IntlMessages id="pages.app-service" />
        </Label>
        <Input
          id="service_name"
          value={values.service_name}
          required
          onChange={handleChange}
        />

        <Row className="mt-4">
          <Colxx className="d-flex flex-column justify-content-center">
            <Label className="m-0 align-middle">
              <IntlMessages id="pages.user-is-enabled" />
            </Label>
          </Colxx>
          <Colxx className="d-flex flex-column justify-content-center">
            <CustomInput
              id="is_enabled"
              type="checkbox"
              checked={values.is_enabled}
              onChange={handleToggle}
            />
          </Colxx>
        </Row>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" outline onClick={toggleModal}>
          <IntlMessages id="pages.cancel" />
        </Button>
        <Button
          id="edit-user"
          color="primary"
          className={`btn-shadow btn-multiple-state ${
            loading ? 'show-spinner' : ''
          }`}
          onClick={handleSubmit}
        >
          <span className="spinner d-inline-block">
            <span className="bounce1" />
            <span className="bounce2" />
            <span className="bounce3" />
          </span>
          <span className="label">
            <IntlMessages id="pages.submit" />
          </span>
        </Button>
      </ModalFooter>
    </Modal>
  );
};

const mapStateToProps = ({ authUser, serviceApp }) => {
  const { accessToken } = authUser;
  const { filters } = serviceApp;
  return { accessToken, filters };
};

export default connect(mapStateToProps, {
  getAllServiceAction: getAllService,
})(EditModal);
